<template>
      <div class="mails" :key="forceUpdate">
            <h2>Formdan Gelen İstekler</h2>
            <ul class="responsive-table">
            <li class="table-header">
                <div class="col col-1">Form Başlığı</div>
                <div class="col col-1">Müşteri İsmi</div>
                <div class="col col-3">Telefon Numarası</div>
                <div class="col col-4">Mesaj</div>
                <div class="col col-4">İşlemler</div>
            </li>
            <li class="table-row" v-for="(mail,key) in mails" :key="key">
                <div class="col col-1" data-label="Form Başlığı">{{mail.requestType | toUpperCaseFilter}}</div>
                <div class="col col-1" data-label="Müşteri İsmi">{{mail.name}}</div>
                <div class="col col-3" data-label="Tel NO:">{{mail.telephone}}</div>
                <div class="col col-4" data-label="Mesaj:">{{mail.requestDetail}}</div>
                <div class="col col-4" data-label="İşlemler"> 
                    <i class="fas fa-check" @click="checkTheMail(key)"></i>
                </div>
            </li>
            
            </ul>
    </div>
</template>


<script>
import axios from "axios"
export default {
    data(){
        return{
            mails:[],
            currentData:null,
            forceUpdate:0,
        }
    },
    methods:{
      goToDetailPage(id){
        for (const prop in this.mails){
          if(this.mails[prop].id===id){
            this.currentData=prop;
          }
          console.log(this.currentData);
        }
       
      },
      getMailsFromStorage(){
        axios.get("https://mars-gyo-default-rtdb.firebaseio.com/mails.json?auth=fo4hAyQChgIWTlrFr7amUvd8jhBiiDU4AoP9CO2y")
        .then(data=>{
            this.mails=  data.data;
            this.forceUpdate+=1;
        })
        .catch(err=>{
          console.log(err);
        })
        
      },
      checkTheMail(key){
        if(confirm("Bu işlem geri alınamaz! Eğer devam etmek istiyorsanız onaylayınız")){
          axios.delete(`https://mars-gyo-default-rtdb.firebaseio.com/mails/${key}.json?auth=fo4hAyQChgIWTlrFr7amUvd8jhBiiDU4AoP9CO2y`)
          .then(()=>{
            this.$fire({
              title:"Başarılı",
              text:"Silme işleminiz başarılı bu talep bir daha gösterilmeyecek!",
              type:"success"
            })
          })
          .catch(()=>{
            this.$fire({
              title:"Başarısız!",
              text:"Silme işleminiz gerçekleştirilemedi lütfen tekar deneyiniz!",
              type:"warning"
            })
          })
        }
        this.getMailsFromStorage();
      }
    },
    filters:{
        toUpperCaseFilter(value){
            return value.toUpperCase();
        }
    },
    created(){
      this.getMailsFromStorage();
    }

}
</script>


