<template>

<div class="request-form">
    <app-navbar></app-navbar>
    <div class="form">
        <div class="form-header">
            <h3>Talebinizi İletin</h3>
            <img src="../assets/undraw_delivery_address_03n0.png">
            <p>Talebinizi bize iletin size yardımcı olalım.</p>
        </div>

        <form class="form-area" @submit.prevent="submitForRequest()">
            <input type="text" v-model="name" required placeholder="İsminizi ve Soyisminizi Giriniz">
            <input type="text" v-model="telephone" required placeholder="Telefon Numaranızı Giriniz">
            <select name="request-type" v-model="requestType" id="request-type">
                <option value="konut" selected>Konut</option>
                <option value="ticari">Ticari</option>
                <option value="arsa">Arsa</option>
            </select>
            <textarea name="requestDetail" v-model="requestDetail" placeholder="Talebinizle ilgili detayları giriniz!"></textarea>
            <button type="submit">Gönder</button>
        </form>
        
    </div>


</div>
    
</template>

<script>
import Navbar from "./Navbar.vue"
import axios from "axios";
import uniqid from "uniqid";
export default {
    data(){
        return{
            name:"",
            telephone:"",
            requestType:"konut",
            requestDetail:""
        }
    },
    components:{
        appNavbar:Navbar
    },
    methods:{
        submitForRequest(){
            let newData={
                id:uniqid(),
                name:this.name,
                telephone:this.telephone,
                requestType:this.requestType,
                requestDetail:this.requestDetail
            }
            axios.post("https://mars-gyo-default-rtdb.firebaseio.com/mails.json?auth=fo4hAyQChgIWTlrFr7amUvd8jhBiiDU4AoP9CO2y",newData)
                .then(()=>{
                    this.$fire({
                        title:"Başarılı",
                        text:"Talebiniz bize iletildi en kısa sürede dönüş yapacağız.",
                        type:"success"
                    });
                    this.name="";
                    this.telephone="";
                    this.requestType="konut",
                    this.requestDetail=""

                })
                .catch(()=>{
                    this.$fire({
                        title:"Başarısız!",
                        text:"Bağlantınızı kontrol ediniz talebiniz gönderilemedi!",
                        type:"warning"
                    })
                })
        }
    }
}
</script>

