import Vue from "vue";
import VueRouter from "vue-router";
import Home from "./views/Home.vue"
import RequestForm from "./components/RequestForm.vue";
import AdminLoginPage from "./components/Admin/AdminLoginPage.vue"
import AdminTempalate from "./components/Admin/AdminTemplate.vue"
import AdminHome from "./components/Admin/AdminHome.vue"
import AdminRequests from "./components/Admin/AdminRequests.vue"
import AdminAddUser from "./components/Admin/AdminAddUser.vue"
import NotFound from "./components/404.vue";
Vue.use(VueRouter);


export const router =new VueRouter({
    routes:[
         {path:"/",component:Home},
         {path:"/talep",component:RequestForm},
         {path:"/admin",component:AdminLoginPage},
         {path:"/admin/adminpanel",component:AdminTempalate,children:[
             {path:"",component:AdminHome},
             {path:"requests",component:AdminRequests},
             {path:"adduser",component:AdminAddUser}
         ]},
         {path:"*",component:NotFound}
    ],
    mode:"history"
});


export default router;
